import React, { Fragment } from 'react';
import './Home.css';
import NotificationComponent from '../NotificationComponent';
import { useState, useEffect } from 'react';
import { BASE_URL } from '../../../CONFIG';
import axios from 'axios'
import ReloadError from '../../UI/ReloadError';
import Loading from '../../UI/Loading';


const Home = props => {

    const [loading, changeLoading] = useState(false);
    const [dashboardContent, changeDashboardContent] = useState(null);
    const [error, changeError] = useState(false);

    const [userDashboard, changeUserDashboard] = useState(null);


    const loadDashboard = () => {
        changeLoading(true);
        changeError(false);
        const url = BASE_URL + '/api/dashboard/';
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeLoading(false);
                changeDashboardContent(res.data);
            })
            .catch(err => {
                if (err.response && (err.response.status === 401)) {
                    props.history.replace('/SignIn');
                    // return localStorage.clear()
                    return
                }
                changeLoading(false);
                changeError(true);
            })
    }

    const loadUserStats = () => {
        const url = BASE_URL + '/api/user-dashboard/';
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeUserDashboard(res.data);
            })
    }

    useEffect(() => {
        loadDashboard();
        loadUserStats();
    }, [])

    let dashboard;

    if (loading) {
        dashboard = <Loading />
    } else if (dashboardContent) {
        dashboard = (
            <Fragment>
                <NotificationComponent content={dashboardContent.notification} />
                <div className="Announcement">
                    <h3>Announcement</h3>
                    {dashboardContent.announcements.map(announcement => (
                        <div className="NumItem" key={announcement.id} >
                            <div className="Num">{announcement.date}</div>
                            <p>{announcement.content}</p>
                        </div>
                    ))}
                </div>
                <div className="PhaseProgress">
                    <div className="Phase1">
                        <h3>{dashboardContent?dashboardContent.phase1_title:''}</h3>
                        <p>{dashboardContent?dashboardContent.phase1_sub_title:''}</p>
                        <div className="Progress">
                            <div style={{ width: userDashboard ? `${userDashboard.phase1_progress_percentage * 100}%` : 0 }} className="Full"></div>
                            <div className="Empty"></div>
                        </div>
                        <div className="Percentage">{userDashboard? userDashboard.phase1_progress_percentage * 100: 0}%</div>
                        <h1>Phase 1</h1>
                    </div>
                    <div className="Phase2">
                        <h3>{dashboardContent?dashboardContent.phase2_title:''}</h3>
                        <p>{dashboardContent?dashboardContent.phase2_sub_title:''}</p>
                        <div className="Progress">
                            <div style={{ width: userDashboard ? `${userDashboard.phase2_progress_percentage * 100}%` : 0 }} className="Full"></div>
                            <div className="Empty"></div>
                        </div>
                        <div className="Percentage">{userDashboard? userDashboard.phase2_progress_percentage * 100: 0}%</div>
                        <h1>Phase 2</h1>
                    </div>
                    <div className="Phase3">
                        <h3>{dashboardContent?dashboardContent.phase3_title:''}</h3>
                        <p>{dashboardContent?dashboardContent.phase3_sub_title:''}</p>
                        <div className="Progress">
                            <div style={{ width: userDashboard ? `${userDashboard.phase3_progress_percentage * 100}%` : 0 }} className="Full"></div>
                            <div className="Empty"></div>
                        </div>
                        <div className="Percentage">{userDashboard? userDashboard.phase3_progress_percentage * 100: 0}%</div>
                        <h1>Phase 3</h1>
                    </div>
                </div>

                <div className="Announcement">
                    <h3>Upcomming Events</h3>
                    {dashboardContent.upcomming_events.map(upcomming_event => (
                        <div className="NumItem" key={upcomming_event.id} >
                            <div className="Num">{upcomming_event.date}</div>
                            <p>{upcomming_event.content}</p>
                        </div>
                    ))}
                </div>
            </Fragment>
        )
    } else if (error) {
        dashboard = <ReloadError reload={loadDashboard} />
    }

    return (
        <div className="Home">
            {dashboard}
        </div>
    )
}

export default Home;