import React, { Fragment } from 'react';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import './Testimonial.css';
import LineHeader from '../UI/LineHeader';
import TestimonialCount from '../../assets/images/testimonial_count.png';
import Play from '../../assets/images/play.png';
import { useState, useEffect } from 'react';
import { BASE_URL, showImg } from '../../CONFIG';
import axios from 'axios';
import Loading from '../UI/Loading';
import ReloadError from '../UI/ReloadError';
import VideoPlayer from '../UI/VideoPlayer';
import SideNav from '../SideNav/SideNav';
import Backdrop from '../SideNav/Backdrop';



const Index = props => {

    const [testimonialContent, changeTestimonialContent] = useState(null);
    const [showVideo, changeShowVideo] = useState(false);
    const [videoUrl, changeVideoUrl] = useState('');
    const [sideNav, toggleSideNav] = useState(false);

    const [loading, changeLoading] = useState(false);

    const [error, changeError] = useState(false);

    const loadTestimonial = () => {
        const url = BASE_URL + '/api/testimonial/';
        changeLoading(true);
        changeError(false);
        axios.get(url)
            .then(res => {
                changeLoading(false);
                changeTestimonialContent(res.data);
            })
            .catch(err => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadTestimonial();
    }, [])

    let testimonial;
    if (loading) {
        testimonial = <Loading />
    }
    else if (testimonialContent) {
        
        testimonial = (
            <Fragment>
                {showVideo? <VideoPlayer url={showImg(videoUrl)} close={() => changeShowVideo(false)} />: null}
                <div className="TestimonialPage">
                    <LineHeader title="SUCCESS STORY" />
                    <div className="TestimonialHeader">
                        <h1>{testimonialContent.heading}</h1>
                    </div>
                </div>
                <div className="Body TestimonialPageContainer">
                    <div className="TestimonialBody">
                        <div className="TestimonialAnalytics">
                            <img src={TestimonialCount} alt=""/>
                            <div className="Accuracy"><div>{testimonialContent.accuracy}%</div><div className="Text">Accuracy</div></div>
                            <div className="Ppp"><div>{testimonialContent.pips_in_profit}+</div><div className="Text">pip in profit per month</div></div>
                            <div className="TradesPerWeek"><div>{testimonialContent.trades_per_week}</div><div className="Text">Trades given in a week</div></div>
                        </div>
                        <div className="TestimonialVideo">
                            <div>
                                <img src={showImg(testimonialContent.poster1)} width="100%"
                                    onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                                    onMouseLeave={e => e.target.style.transform = "scale(1)"}
                                    className="Image" alt="Poster 1" />
                                <img src={Play} onClick={() => {
                                    changeVideoUrl(testimonialContent.video1);
                                    changeShowVideo(true)
                                }} alt="Play" className="Play" />
                            </div>
                        </div>
                        <div className="TestimonialVideo">
                            <div>
                                <img src={showImg(testimonialContent.poster2)} width="100%"
                                    onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                                    onMouseLeave={e => e.target.style.transform = "scale(1)"}
                                    className="Image" alt="Poster 2" />
                                <img src={Play} onClick={() => {
                                    changeVideoUrl(testimonialContent.video2);
                                    changeShowVideo(true)
                                }} alt="Play" className="Play" />
                            </div>
                        </div>
                        <div className="TestimonialVideo">
                            <div>
                                <img src={showImg(testimonialContent.poster3)} width="100%"
                                    onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                                    onMouseLeave={e => e.target.style.transform = "scale(1)"}
                                    className="Image" alt="Poster 3" />
                                <img src={Play} onClick={() => {
                                    changeVideoUrl(testimonialContent.video3);
                                    changeShowVideo(true)
                                }} alt="Play" className="Play" />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="TestimonialContainer">
                    <Footer data={{
                        twitter_url: testimonialContent.twitter_url,
                        facebook_url: testimonialContent.facebook_url,
                        instagram_url: testimonialContent.instagram_url,
                    }} />
                </div>
            </Fragment>
        )
    }
    else if (error) {
        testimonial = <ReloadError reload={loadTestimonial} />
    }

    return (
        <div className="ImgBackground">
            <div>
                <Header toggleSideNav={val => toggleSideNav(val)} />
                <SideNav show={sideNav} />
                <Backdrop show={sideNav} close={() => toggleSideNav(false)} />
                {testimonial}
            </div>
        </div>
    )
}

export default Index;