import React from 'react';
import './SideNav.css';
import Logo from '../../../assets/images/Quest_FF-04@2x.png';
import { useState } from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import { useEffect } from 'react';
import { BASE_URL } from "../../../CONFIG";
import axios from 'axios';


const SideNav = props => {

    const [dashboardContent, changeDashboardContent] = useState(null);

    const loadDashboard = () => {
        const url = BASE_URL + '/api/user-dashboard/';
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeDashboardContent(res.data);
            })
    }

    useEffect(() => {
        loadDashboard();
    }, [])

    const logout = e => {
        e.preventDefault();
        localStorage.clear();
        props.history.replace('/');
    }

    return (
        <div className="DashboardSideNav" style={{ transform: !props.showSideNav ? 'translateX(-100vw)' : 'translateX(0)' }} >
            <img src={Logo} alt="" srcset="" />
            <div className="ProfileImg">
                <p>{dashboardContent?dashboardContent.username:'Welcome'}</p>
            </div>
            <div className="SideNavItems">
                <NavLink onClick={props.toggleSideNav} to='/' exact={true} activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-home"></i>
                    Back To Home
                </NavLink>
                <NavLink onClick={props.toggleSideNav} to='/Dashboard' exact={true} activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-home"></i>
                    Dashboard
                </NavLink>
                <NavLink onClick={props.toggleSideNav} to='/Dashboard/phase/1' activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-chart-bar"></i>
                    Phase 1
                </NavLink>
                <NavLink onClick={props.toggleSideNav} to='/Dashboard/phase/2' activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-chart-bar"></i>
                    Phase 2
                </NavLink>
                <NavLink onClick={props.toggleSideNav} to='/Dashboard/phase/3' activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-chart-bar"></i>
                    Phase 3
                </NavLink>
                <NavLink onClick={props.toggleSideNav} to='/Dashboard/Training' activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-book"></i>
                    Training
                </NavLink>
                <NavLink onClick={props.toggleSideNav} to='/Dashboard/Materials' activeClassName="ActiveSideNav" className="SideNavItem">
                    <i className="fas fa-file"></i>
                    Materials
                </NavLink>
                <a className="SideNavItem" href="#index" onClick={logout} >
                    <i className="fas fa-power-off"></i>
                    Log Out
                </a>
            </div>
        </div>
    )
}

export default withRouter(SideNav);