import React from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Landing from './components/Landing/Index';
import Pioneers from './components/Pioneers/Index';
import GetStarted from './components/GetStarted/Index';
import Testimonials from './components/Testimonial/Index';
import SignIn from './components/SignIn/SignIn';
import SignUp from './components/SignUp/SignUp';
import Dashboard from './components/Dashboard/Dashboard';
import Err from './components/404/404';

function App() {

  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path='/Pioneers' component={() => <Pioneers />} />
          <Route path='/GetStarted' component={() => <GetStarted />} />
          <Route path='/Testimonials' component={() => <Testimonials />} />
          <Route path='/SignIn' component={() => <SignIn />} />
          <Route path='/SignUp' component={() => <SignUp />} />
          <Route path='/Dashboard' component={() => <Dashboard />} />
          <Route exact path='/' component={() => <Landing />} />
          <Route path='' component={Err} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
