import React, { useState } from 'react';


export default props => {

    const [arrowMove, changeArrowMove] = useState(false);

    return (
        <div className="GetStarted">
            <h1>Lets get you started, Schedule a call</h1>
            <p>Take control of your financial stand and experience freedom beyond fears and
            debts accumulation, Take control of your financial stand and experience
            freedom beyond Take control of your financial stand and experience freedom
            beyond fears and debts accumulation, Take control of your financial stand
                  and experience freedom beyond</p>
            <a href="#LearnMore" onMouseEnter={() => changeArrowMove(true)}
                onMouseLeave={() => changeArrowMove(false)}
                className="Button">Get Started <span style={{
                    marginRight: arrowMove ? 0 : 10,
                    marginLeft: arrowMove ? 30 : 20
                }}
                ><i className="fas fa-arrow-right"></i></span></a>
        </div>
    )
}