import React, { useState } from 'react';
import Header from '../Header/Header';
import './SignIn.css';
import { Link, withRouter } from 'react-router-dom';
import axios from 'axios';
import { BASE_URL } from "../../CONFIG";
import SideNav from '../SideNav/SideNav';
import Backdrop from '../SideNav/Backdrop';



const Index = props => {

    const [formData, changeFormData] = useState({
        username: '',
        password: ''
    });

    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);
    const [sideNav, toggleSideNav] = useState(false);

    const updateFormData = (field, value) => {
        changeFormData({ ...formData, [field]: value })
    }

    const submitForm = (e) => {
        e.preventDefault();
        changeLoading(true);
        changeError(false);
        const url = BASE_URL + '/api/login/';
        axios.post(url, formData)
            .then((res) => {
                changeLoading(false);
                localStorage.setItem('token', res.data.token)
                localStorage.setItem('id', res.data.userid)
                props.history.push('/Dashboard')
            })
            .catch((err) => {
                changeLoading(false);
                changeError(true);
            })
    }

    return (
        <div className="ImgBackground">
            <div>
                <Header signin={true} toggleSideNav={val => toggleSideNav(val)} />
                <SideNav show={sideNav} />
                <Backdrop show={sideNav} close={() => toggleSideNav(false)} />
                <div className="GetStartedHeader SignIn">
                    {error ? <p style={{ color: 'tomato' }}>Incorrect username and or password</p> : null}
                    <form onSubmit={submitForm} autoComplete="off" className="SignInForm">
                        <h4 style={{ textAlign: 'left' }}>Sign in to an account</h4>

                        <p>Username</p>
                        <input type="text" value={formData.username} onChange={e => updateFormData('username', e.target.value)} required />
                        <p>Password</p>
                        <input type="password" required value={formData.password} onChange={e => updateFormData('password', e.target.value)} />

                        <button disabled={loading} type="submit">Sign In</button>

                        <div className="ForgotPassword">
                            <Link to='/ForgotPassword'>Forgot Password</Link>
                        </div>
                    </form>
                    <Link to="/SignUp" className="NavigateLoginSignup">New here? Create an account</Link>
                </div>
            </div>
        </div>
    )
}

export default withRouter(Index);