import React, { Fragment } from 'react';
import './Materials.css';
import NotificationComponent from '../NotificationComponent';
import { useState, useEffect } from 'react';
import Material from './Material';
import { BASE_URL } from "../../../CONFIG";
import axios from 'axios';
import Loading from '../../UI/Loading';
import ReloadError from '../../UI/ReloadError';


const Materials = props => {

    const [current, changeCurrent] = useState('');
    const [loading, changeLoading] = useState(false);
    const [materialsContent, changeMaterialsContent] = useState(null);
    const [error, changeError] = useState(false);
    const [dashboardContent, changeDashboardContent] = useState(null);
    const [page, changePage] = useState(1);
    const [previousPage, changePreviousPage] = useState(null);
    const [nextPage, changeNextPage] = useState(null);

    const loadDashboard = () => {
        changeLoading(true);
        changeError(false);
        const url = BASE_URL + '/api/dashboard/';
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeLoading(false);
                changeDashboardContent(res.data);
            })
    }


    const loadTraninings = () => {
        changeLoading(true);
        changeError(false);
        let url = BASE_URL + `/api/materials/?page_size=20&page=${page}&type=${current}`;
        
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeLoading(false);
                changeMaterialsContent(res.data.results);
                if (res.data.next) {
                    changeNextPage(page + 1);
                } else {
                    changeNextPage(null);
                }
                if (res.data.previous) {
                    changePreviousPage(page - 1);
                } else {
                    changePreviousPage(null);
                }
            })
            .catch(err => {
                if (err.response && (err.response.status === 401)) {
                    props.history.replace('/SignIn');
                    // return localStorage.clear()
                    return
                }
                if (err.response && (err.response.status === 404)) {
                    console.log('not found');
                    return
                }
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadTraninings();
    }, [page, current])
    useEffect(() => {
        loadDashboard();
    }, [])

    const changeFileType = (type) => {
        changeCurrent(type);
    }

    let materials;

    if (loading) {
        materials = <Loading />
    } else if (materialsContent) {
        materials = (
            <Fragment>
                <NotificationComponent content={dashboardContent ? dashboardContent.notification : ''} />
                <div className="MaterialFilter">
                    <div onClick={() => changeFileType('')} className={current === '' ? "ActiveFilter" : ""}>ALL</div>
                    <div onClick={() => changeFileType('PDF')} className={current === 'PDF' ? "ActiveFilter" : ""}>PDF</div>
                    <div onClick={() => changeFileType('PPT')} className={current === 'PPT' ? "ActiveFilter" : ""}>PPT</div>
                    <div onClick={() => changeFileType('PNG')} className={current === 'PNG' ? "ActiveFilter" : ""}>PNG</div>
                </div>
                <div className="Materials">
                    {materialsContent.map(material => <Material key={material.id} 
                    type={material.material_type} url={material.file} title={material.title} />)}
                </div>
                <div className="PreviousNext">
                    <button className="Button Prev" onClick={previousPage?() => changePage(page - 1):null} 
                    style={{opacity: previousPage?1:.5, cursor: previousPage?'pointer':'not-allowed'}} >Previous</button>
                    <button className="Button Next" onClick={nextPage?() => changePage(page + 1):null} 
                    style={{opacity: nextPage?1:.5, cursor: nextPage?'pointer':'not-allowed'}} >Next</button>
                </div>
            </Fragment>
        )
    } else if (error) {
        materials = <ReloadError reload={() => {
            loadTraninings();
            loadDashboard();
        }} />
    }

    return (
        <div className="MaterialContainer">
            {materials}
        </div>
    )
}

export default Materials;