import React from 'react';
import Play from '../../assets/images/play.png';
import { showImg } from '../../CONFIG';
import { useState } from 'react';
import VideoPlayer from '../UI/VideoPlayer';


export default props => {

    const [showVideo, changeShowVideo] = useState(false);

    return (
        <div className="LearningContainer">
            {showVideo? <VideoPlayer url={showImg(props.data.learning_video)} close={() => changeShowVideo(false)} />: null}
            <div className="Learning About">
                <div className="ImageContainer">
                    <img src={showImg(props.data.learning_poster)} width="100%"
                        onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                        onMouseLeave={e => e.target.style.transform = "scale(1)"}
                        className="Image" alt="Learning" />
                    <img src={Play} onClick={() => changeShowVideo(true)} alt="Play" className="Play" />
                </div>
                <div className="AboutContent LearningContent">
                    <div className="AboutHeader">
                        <p>LEARNING</p>
                        <div className="AboutLine"></div>
                    </div>
                    <h1>{props.data.learning_heading}</h1>
                    <p>
                        {props.data.learning_text}
                    </p>
                </div>
            </div>
        </div>
    )

}
