import React, { useState, useEffect, Fragment } from 'react';
import './Phase.css';
import NotificationComponent from '../NotificationComponent';
import Play from '../../../assets/images/play.png';
import { BASE_URL, showImg } from "../../../CONFIG";
import axios from 'axios';
import Loading from '../../UI/Loading';
import ReloadError from '../../UI/ReloadError';
import { withRouter } from 'react-router-dom';
import VideoPlayer from '../../UI/VideoPlayer';


const Phase = props => {
    // id -> /Dashboard/phase/:id
    const id = props.match.params.id;

    const [loading, changeLoading] = useState(false);
    const [phaseContent, changePhaseContent] = useState(null);
    const [error, changeError] = useState(false);
    const [currentVideo, changeCurrentVideo] = useState(0);

    const [showVideo, changeShowVideo] = useState(false);


    const loadPhase = () => {
        changeLoading(true);
        changeError(false);
        const url = BASE_URL + `/api/phase/${id}/`;
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeLoading(false);
                changePhaseContent(res.data);
                changeCurrentVideo(0);
            })
            .catch(err => {
                if (err.response && (err.response.status === 401)) {
                    props.history.replace('/SignIn');
                    // return localStorage.clear()
                    return
                }
                if (err.response && (err.response.status === 404)) {
                    console.log('not found');
                    return
                }
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadPhase();
    }, [id])

    let phase;

    if (loading) {
        phase = <Loading />
    } else if (phaseContent) {
        phase = (
            <Fragment>
                <NotificationComponent content={phaseContent.notification} />
                {showVideo? <VideoPlayer onEnded={() => alert('ended')} url={showImg(phaseContent.content[currentVideo].video)} close={() => changeShowVideo(false)} />: null}
                <div className="PhaseContainer">
                    <div className="PhaseVideo">
                        <div className="ImageContainer">
                            <img src={showImg(phaseContent.content[currentVideo].poster)} width="100%"
                                onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                                onMouseLeave={e => e.target.style.transform = "scale(1)"}
                                className="Image" alt="AboutImg" />
                            <img src={Play} onClick={() => changeShowVideo(true)} alt="Play" className="Play" />
                        </div>
                        <div className="PhaseVideoDescription">
                            <h3>Description</h3>
                            <p>{phaseContent.content[currentVideo].description}</p>
                        </div>
                    </div>
                    <div className="CourseContent">
                        <div className="CourseContentHeading">
                            <h2>Course Content</h2>
                        </div>
                        <div className="CourseItems">
                            {phaseContent.content.map((content, i) => (
                                <div style={{background: currentVideo===i?'#9577CB6E 0% 0% no-repeat padding-box':null}} key={content.id} onClick={() => {
                                    changeCurrentVideo(i);
                                }} >
                                    <h3>{(i+1) < 10?'0' + (i+1): i+1}</h3>
                                    <p>{content.title}</p>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    } else if (error) {
        phase = <ReloadError reload={loadPhase} />
    }

    return (
        <div className="Phase">
            {phase}
        </div>
    )
}

export default withRouter(Phase);