import React, { Fragment } from 'react';
import './SideNav.css';
import { Link, withRouter } from 'react-router-dom';


export default withRouter((props) => {

    return (
        <div className="SideNav" style={{ transform: props.show ? 'translateX(0)' : 'translateX(-100vw)' }} >
            <Link to='/'>Home</Link>
            <Link to='/Testimonials'>Testimonials</Link>
            <Link to='/Pioneers'>Pioneers</Link>
            <Link to='/GetStarted'>Get Started</Link>
            <div className="Divider"></div>
            {!localStorage.getItem('token') ? <Fragment>
                <Link to='/SignIn' className="Button">Sign In</Link>
                <Link to='/SignUp' className="Button">Sign Up</Link>
            </Fragment> :
                <Fragment>
                    <Link to='/Dashboard' className="Button">Dashboard</Link>
                    <a href="#index" className="Button" onClick={e => {
                        e.preventDefault();
                        localStorage.clear();
                        props.history.replace('/');
                    }} >Logout</a>
                </Fragment>
            }
        </div>
    )
})
