import React, { Fragment } from 'react';
import Pioneers from './Pioneers';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import LineHeader from '../UI/LineHeader';
import Leaders from './Leaders';
import SuccessStory from './SuccessStory';
import GetStarted from './GetStarted';
import { BASE_URL } from "../../CONFIG";
import axios from 'axios';
import { useState, useEffect } from 'react';
import ReloadError from '../UI/ReloadError';
import Loading from '../UI/Loading';
import SideNav from '../SideNav/SideNav';
import Backdrop from '../SideNav/Backdrop';



const Index = props => {

    const [pioneerContent, changePioneerContent] = useState(null);
    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);
    const [sideNav, toggleSideNav] = useState(false);

    const loadPioneer = () => {
        const url = BASE_URL + '/api/pioneer/';
        changeLoading(true);
        changeError(false);
        axios.get(url)
            .then(res => {
                changeLoading(false);
                changePioneerContent(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadPioneer();
    }, [])

    let pioneers;

    if (loading) {
        pioneers = <Loading />
    } else if (pioneerContent) {
        pioneers = (
            <Fragment>
                <Pioneers heading={pioneerContent.heading} />
                <div className="Body">
                    <div className="PioneersBody">
                        <LineHeader title="LEADERS" />
                        <Leaders leaders={pioneerContent.leaders} />
                    </div>
                    <SuccessStory stories={pioneerContent.success_stories} />
                </div>
            </Fragment>
        )
    } else if (error) {
        pioneers = <ReloadError reload={loadPioneer} />
    }

    return (
        <div className="ImgBackground">
            <div>
                <Header toggleSideNav={val => props.toggleSideNav(val)} />
                <SideNav show={sideNav} />
                <Backdrop show={sideNav} close={() => toggleSideNav(false)} />
                {pioneers}
                <div className="TestimonialContainer">
                    <GetStarted data={pioneerContent ? pioneerContent.schedule_text : 'Loading...'} />
                    <Footer data={{
                        twitter_url: pioneerContent ? pioneerContent.twitter_url : '',
                        instagram_url: pioneerContent ? pioneerContent.instagram_url : '',
                        facebook_url: pioneerContent ? pioneerContent.facebook_url : '',
                    }} />
                </div>
            </div>
        </div>
    )
}

export default Index;