import React, { useState } from 'react';
import './Dashboard.css';
import SideNav from './SideNav/SideNav';
import Home from './Home/Home';
import { Switch, Route, withRouter } from 'react-router-dom';
import Phase from './Phase/Phase';
import Traninings from './Traninings/Traninings';
import Materials from './Materials/Materials';
import Err from '../404/404';


const Dashboard = props => {

    const [showSideNav, toggleSideNav] = useState(false);

    return (
        <div className="Dashboard">
            <SideNav showSideNav={showSideNav} toggleSideNav={() => toggleSideNav(!showSideNav)} />
            <div
                onClick={() => toggleSideNav(!showSideNav)} className={showSideNav ? "DashboardMenu DashboardMenuTranslate" : "DashboardMenu"}>
                <div></div>
                <div></div>
                <div></div>
            </div>
            <div className="Content">
                <Switch>
                    <Route path='/Dashboard/phase/:id' component={Phase} />
                    <Route path='/Dashboard/Training' component={Traninings} />
                    <Route path='/Dashboard/Materials' component={Materials} />
                    <Route exact path='/Dashboard' component={Home} />
                    <Route path='' component={() => <Err dashboard />} />
                </Switch>
            </div>
        </div>
    )
}

export default withRouter(Dashboard);