import React, { Fragment } from 'react';
import './Pioneers.css';


const Pioneers = props => {

    return (
        <Fragment>
            <div className="PioneersPageContainer">
                <div className="PioneersPage">
                    <h1 className="PioneersHeading">
                        {props.heading}
                    </h1>
                </div>
            </div>
        </Fragment>
    )
}

export default Pioneers;