import React from 'react';
import './404.css';
import { withRouter, Link } from 'react-router-dom';


const Error = props => (
    <div className={props.dashboard?"DashboardErr":"Err"}>
        <div>
            <h1>4</h1>
            <h1>0</h1>
            <h1>4</h1>
        </div>
        <div>
            <h3>Page Not Found!</h3>
            <Link to={props.dashboard?'/Dashboard':'/'} className="Button">Return To Home</Link>
        </div>
    </div>
)

export default withRouter(Error);