import React from 'react';
import LineHeader from '../UI/LineHeader';
import { showImg } from '../../CONFIG';


const SuccessStory = props => (
    <div className="SuccessStory">
        <div className="SuccessHeader">
            <LineHeader title="SUCCESS STORY" />
            <h1>Those who have brought us this far</h1>
        </div>
        <div className="SuccessImages">
            {props.stories.map(story => <img src={showImg(story.image)} alt={story.id} />)}
        </div>
    </div>
)

export default SuccessStory;