import React from 'react';
import Play from '../../assets/images/play.png';
import { showImg } from '../../CONFIG';
import VideoPlayer from '../UI/VideoPlayer';
import { useState } from 'react';


export default (props) => {
    
    const [showVideo, changeShowVideo] = useState(false);

    return (
        <div className="About">
            {showVideo? <VideoPlayer url={showImg(props.data.about_video)} close={() => changeShowVideo(false)} />: null}
            <div className="ImageContainer">
                <img src={showImg(props.data.about_poster)} width="100%"
                    onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                    onMouseLeave={e => e.target.style.transform = "scale(1)"}
                    className="Image" alt="about" />
                <img onClick={() => changeShowVideo(true)} src={Play} alt="Play" className="Play" />
            </div>
            <div className="AboutContent">
                <div className="AboutHeader">
                    <p>ABOUT</p>
                    <div className="AboutLine"></div>
                </div>
                <h1>{props.data.about_heading}</h1>
                <p>
                    {props.data.about_text}
                </p>
            </div>
        </div>
    )
}
