import React from 'react';
import ExplainCard from './ExplainCard';


export default props => (
    <div className="ExplainItemsContainer">
        <div className="ExplainItems">
            <ExplainCard>{props.data.learning_item1}</ExplainCard>
            <ExplainCard>{props.data.learning_item2}</ExplainCard>
            <ExplainCard>{props.data.learning_item3}</ExplainCard>
            <ExplainCard>{props.data.learning_item4}</ExplainCard>
        </div>
    </div>
)