import React from 'react';
import Header from '../Header/Header';
import { Link, withRouter } from 'react-router-dom';
import { BASE_URL } from "../../CONFIG";
import { useState } from 'react';
import Notification from '../UI/Notification';
import axios from 'axios';
import SideNav from '../SideNav/SideNav';
import Backdrop from '../SideNav/Backdrop';



const Index = props => {

    const [formData, changeFormData] = useState({
        username: '',
        password: '',
        confirmPassword: '',
        email: ''
    });

    const [errorData, changeErrorData] = useState({
        username: '',
        password: '',
        confirmPassword: '',
        email: ''
    });

    const [loading, changeLoading] = useState(false);
    const [success, changeSuccess] = useState(false);
    const [error, changeError] = useState(false);
    const [sideNav, toggleSideNav] = useState(false);

    const updateFormField = (field, value) => {
        changeErrorData({...errorData, [field]: ''});
        changeFormData({ ...formData, [field]: value });
    }

    const updateErrorField = (field, value) => {
        changeErrorData({ ...errorData, [field]: value })
    }

    const submitForm = (e) => {
        e.preventDefault();
        console.log(formData.password, formData.confirmPassword)
        if (formData.password.trim() !== formData.confirmPassword.trim()) {
            changeErrorData({...errorData, password: 'password do not match!'});
            return 
        }
        changeLoading(true);
        changeError(false);
        const url = BASE_URL + '/api/user-create/';
        axios.post(url, formData)
            .then(() => {
                changeLoading(false);
                changeSuccess(true);
                setTimeout(() => {
                    props.history.push('/SignIn')
                }, 3000);
            })
            .catch((err) => {
                changeLoading(false);
                changeSuccess(true);
                changeError(true);
                for (let field in err.response.data) {
                    updateErrorField(field, err.response.data[field]);
                }
            })
    }

    let form = (
        <div className="GetStartedHeader SignIn">
            <Notification show={success} error={error} >{error ? 'an error occured! Please, Try again' :
                'account has been created successfully!'}</Notification>
            <form autoComplete="off" onSubmit={submitForm} className="SignInForm">
                <h4 style={{ textAlign: 'left' }}>Sign in to an account</h4>

                <p>Email</p>
                {errorData.email ? <p className="Error">{errorData.email}</p> : null}
                <input type="email" value={formData.email} onChange={e => updateFormField('email', e.target.value)} required />
                <p>Username</p>
                {errorData.username ? <p className="Error">{errorData.username}</p> : null}
                <input type="text" value={formData.username} onChange={e => updateFormField('username', e.target.value)} required />
                <p>Password</p>
                {errorData.password ? <p className="Error">{errorData.password}</p> : null}
                <input type="password" value={formData.password} onChange={e => updateFormField('password', e.target.value)} required />
                <p>Confirm Password</p>
                <input type="password" value={formData.confirmPassword} onChange={e => updateFormField('confirmPassword', e.target.value)} required />

                <button disabled={loading} type="submit">Sign In</button>

                <div className="ForgotPassword">
                    <Link to='/ForgotPassword'>Forgot Password</Link>
                </div>
            </form>
            <Link to="/SignUp" className="NavigateLoginSignup">New here? Create an account</Link>
        </div>
    )

    return (
        <div className="ImgBackground">
            <div>
                <Header signin={false} toggleSideNav={val => toggleSideNav(val)} />
                <SideNav show={sideNav} />
                <Backdrop show={sideNav} close={() => toggleSideNav(false)} />
                {form}
            </div>
        </div>
    )
}

export default withRouter(Index);