import React, { Fragment } from 'react';
import './Landing.css';
import { useState } from 'react';
import About from './About';
import OurWays from './OurWays';
import Learning from './Learning';
import ExplainItems from './ExplainItems';
import { showImg } from '../../CONFIG';


const Landing = props => {

    const [arrowMove, changeArrowMove] = useState(false);

    const {
        about_heading,
        about_poster,
        about_text,
        about_video,
        our_way_heading,
        forex_text,
        crypto_text,
        learning_heading,
        learning_text,
        learning_poster,
        learning_video,
        learning_item1,
        learning_item2,
        learning_item3,
        learning_item4
    } = props.data;

    const aboutData = {
        about_heading,
        about_poster,
        about_text,
        about_video
    }

    const ourWaysData = {
        our_way_heading,
        forex_text,
        crypto_text,
    }

    const learningData = {
        learning_heading,
        learning_text,
        learning_poster,
        learning_video
    }

    const explainItemsData = {
        learning_item1,
        learning_item2,
        learning_item3,
        learning_item4,
    }

    return (
        <Fragment>
            <div>
                <div className="Landing">
                    <div className="FinancialFreedomText">
                        <h1>{props.data.heading1}</h1>
                        <p>{props.data.text1}</p>
                        <a href="#LearnMore" onMouseEnter={() => changeArrowMove(true)}
                            onMouseLeave={() => changeArrowMove(false)}
                            className="Button">Learn more <span style={{
                                marginRight: arrowMove ? 0 : 10,
                                marginLeft: arrowMove ? 30 : 20
                            }}
                            ><i className="fas fa-arrow-right"></i></span></a>
                    </div>
                    <div className="PhoneTrade">
                        <img src={showImg(props.data.image1)} alt="phone trade" />
                    </div>
                </div>
                <div className="Body">
                    <About data={aboutData} />
                </div>
                <OurWays data={ourWaysData} />
                <Learning data={learningData} />
                <ExplainItems data={explainItemsData} />
            </div>
        </Fragment>
    )
}

export default Landing;