import React, { useState, useEffect, Fragment } from 'react';
import Landing from './Landing';
import Testimonial from './Testimonial';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import Loading from '../UI/Loading';

import axios from 'axios';
import { BASE_URL } from '../../CONFIG';
import ReloadError from '../UI/ReloadError';
import SideNav from '../SideNav/SideNav';
import Backdrop from '../SideNav/Backdrop';



const Index = props => {

    const [homeContent, changeHomeContent] = useState(null);

    const [loading, changeLoading] = useState(false);

    const [error, changeError] = useState(false);

    const [sideNav, toggleSideNav] = useState(false);


    const loadHomeContent = () => {
        changeLoading(true);
        const url = BASE_URL + '/api/home/';
        axios.get(url)
            .then(res => {
                changeLoading(false);
                changeError(false);
                changeHomeContent(res.data);
            })
            .catch(err => {
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadHomeContent()
    }, [])

    let content;

    if (loading) {
        content = <Loading />
    } else if (homeContent) {
        const { heading1, text1, image1, about_heading, about_text, about_poster, about_video,
            our_way_heading, forex_text, crypto_text, learning_heading, learning_text, learning_video,
            learning_poster, learning_item1, learning_item2, learning_item3, learning_item4,
            testimonial_heading, testimonial_image1, testimonial_image2, testimonial_image3, testimonial_text,
            facebook_url, twitter_url, instagram_url
        } = homeContent;
        const landingData = {
            heading1,
            text1,
            image1,
            about_heading,
            about_poster,
            about_text,
            about_video,
            our_way_heading,
            forex_text,
            crypto_text,
            learning_heading,
            learning_text,
            learning_poster,
            learning_video,
            learning_item1,
            learning_item2,
            learning_item3,
            learning_item4
        }
        const testimonialData = {
            testimonial_heading,
            testimonial_image1,
            testimonial_image2,
            testimonial_image3,
            testimonial_text
        }

        const footerData = {
            facebook_url,
            twitter_url,
            instagram_url
        }
        content = (
            <Fragment>
                <Landing data={landingData} />
                <div className="TestimonialContainer">
                    <Testimonial data={testimonialData} />
                    <Footer data={footerData} />
                </div>
            </Fragment>
        )
    } else if (error) {
        content = (
            <ReloadError reload={loadHomeContent} />
        )
    }

    return (
        <div className="ImgBackground">
            <div>
                <Header toggleSideNav={val => toggleSideNav(val)} />
                <SideNav show={sideNav} />
                <Backdrop show={sideNav} close={() => toggleSideNav(false)} />
                {content}
            </div>
        </div>
    )
}

export default Index;