import React from 'react';
import OurWayImage1 from '../../assets/images/our_way.png';
import OurWayImage2 from '../../assets/images/our_way2.png';
import { useState } from 'react';


export default props => {

    const [image, changeImage] = useState('forex');

    return (
        <div className="OurWays">
            <div>
                <div className="OurWaysContent">
                    <div className="AboutHeader">
                        <p>OUR WAY </p>
                        <div className="AboutLine"></div>
                    </div>
                    <h1>{props.data.our_way_heading}</h1>
                    <div className="ForexCryptoButtons">
                        <div className={image === "forex" ? "Button" : "NegativeButton"}
                            onClick={() => changeImage('forex')}>Forex</div>
                        <div className={image === "crypto" ? "Button" : "NegativeButton"}
                            onClick={() => changeImage('crypto')}>Crypto</div>
                    </div>
                    {image === "forex"?<p>{props.data.forex_text}</p>:
                    <p>{props.data.crypto_text}</p>}
                </div>
                <div className="OurWaysImage">
                    {image === 'forex' ? <img src={OurWayImage1} alt="our way" width="100%" /> :
                        <img src={OurWayImage2} alt="our way" width="100%" />}
                </div>
            </div>
        </div>
    )
}