import React, { Fragment } from 'react';
import './Traninings.css';
import NotificationComponent from '../NotificationComponent';
import Play from '../../../assets/images/play.png';
import { BASE_URL, showImg } from "../../../CONFIG";
import axios from 'axios';
import Loading from '../../UI/Loading';
import ReloadError from '../../UI/ReloadError';
import { useState, useEffect } from "react";
import VideoPlayer from '../../UI/VideoPlayer';


const Traninings = props => {

    const [loading, changeLoading] = useState(false);
    const [trainingContent, changeTrainingContent] = useState(null);
    const [error, changeError] = useState(false);
    const [currentVideo, changeCurrentVideo] = useState(0);

    const [showVideo, changeShowVideo] = useState(false);


    const loadTraninings = () => {
        changeLoading(true);
        changeError(false);
        const url = BASE_URL + `/api/trainings/`;
        const headers = { 'Authorization': `JWT ${localStorage.getItem('token')}` };
        axios.get(url, { headers: headers })
            .then(res => {
                changeLoading(false);
                changeTrainingContent(res.data);
                changeCurrentVideo(0);
            })
            .catch(err => {
                if (err.response && (err.response.status === 401)) {
                    props.history.replace('/SignIn');
                    // return localStorage.clear()
                    return
                }
                if (err.response && (err.response.status === 404)) {
                    console.log('not found');
                    return
                }
                changeLoading(false);
                changeError(true);
            })
    }

    useEffect(() => {
        loadTraninings();
    }, [])

    let trainings;

    if (loading) {
        trainings = <Loading />
    } else if (trainingContent) {
        trainings = (
            <Fragment>
                <NotificationComponent content={trainingContent.notification} />
                {showVideo? <VideoPlayer onEnded={() => alert('ended')} url={showImg(trainingContent.data[currentVideo].video)} close={() => changeShowVideo(false)} />: null}
                <div className="TrainingContainer">
                    <div className="TrainingList">
                        <div className="TrainingItems CourseItems">
                            <h2>Training</h2>
                            {trainingContent.data.map((content, i) => (
                                <div style={{ background: currentVideo === i ? '#9577CB6E 0% 0% no-repeat padding-box' : null }} key={content.id} onClick={() => {
                                    changeCurrentVideo(i);
                                }} >
                                    <h3>{(i + 1) < 10 ? '0' + (i + 1) : i + 1}</h3>
                                    <p>{content.title}</p>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="TrainingVideo">
                        <div className="ImageContainer">
                            <img src={showImg(trainingContent.data[currentVideo].poster)} width="100%"
                                onMouseEnter={e => e.target.style.transform = "scale(1.1)"}
                                onMouseLeave={e => e.target.style.transform = "scale(1)"}
                                className="Image" alt="About" />
                            <img src={Play} onClick={() => changeShowVideo(true)} alt="Play" className="Play" />
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    } else if (error) {
        trainings = <ReloadError reload={loadTraninings} />
    }

    return (
        <div className="Training">
            {trainings}
        </div>
    )
}

export default Traninings;
