import React, { Fragment } from 'react';
import './Header.css';
import { Link, withRouter } from "react-router-dom";
import Logo from '../../assets/images/Quest_FF-04@2x.png';


const Header = props => (
    <div className="Header">
        <div className="Menu" onClick={() => props.toggleSideNav(true)} >
            <div></div>
            <div></div>
            <div></div>
        </div>
        <div className="Logo">
            <img src={Logo} alt="logo" />
        </div>
        <div className="NavItems">
            <Link to='/'>Home</Link>
            <Link to='/Testimonials'>Testimonials</Link>
            <Link to='/Pioneers'>Pioneers</Link>
            <Link to='/GetStarted'>Get Started</Link>
        </div>
        <div className="SignIn">
            {!localStorage.getItem('token')?
            <Link className="SignIn Button" to="/Signin">Sign In</Link>:
            <Fragment>
                <Link className="SignIn Button" to="/Dashboard">Dashboard</Link>
                <a href="#index" onClick={e => {
                    e.preventDefault();
                    localStorage.clear();
                    props.history.replace('/');
                }} style={{marginLeft: 10}} className="SignIn Button">Logout</a>
            </Fragment>}
        </div>
    </div>
)

export default withRouter(Header);