import React, { Fragment } from 'react';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import './GetStarted.css';
import Middle from '../UI/MiddleLineHeader';
import Loading from '../UI/Loading';
import { BASE_URL, showImg } from "../../CONFIG";
import axios from 'axios';
import { useState, useEffect } from 'react';
import ReloadError from '../UI/ReloadError';
import Notification from '../UI/Notification';



const Index = props => {

    const [getStarted, changeGetStarted] = useState(null);
    const [callTime, changeCallTime] = useState(null);

    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const [formValue, changeFormValue] = useState({
        email: '',
        phone_no: '',
        call_time: ''
    })
    const [scheduleFormLoading, changeScheduleFormLoading] = useState(false);
    const [scheduleFormError, changeScheduleFormError] = useState(false);
    const [showNotification, changeShowNotification] = useState(false);

    const updateFormField = (field, value) => {
        changeFormValue({ ...formValue, [field]: value });
    }

    const loadGetStarted = () => {
        const url = BASE_URL + '/api/get-started/';
        changeLoading(true);
        changeError(false);
        axios.get(url)
            .then(res => {
                changeLoading(false);
                changeGetStarted(res.data);
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
            })
    }

    const loadCallTime = () => {
        const url = BASE_URL + '/api/call-time/';
        axios.get(url)
            .then(res => {
                changeCallTime(res.data);
            })
    }

    useEffect(() => {
        loadCallTime();
        loadGetStarted();
    }, [])

    const submitSchedule = (e) => {
        e.preventDefault();
        const url = BASE_URL + '/api/schedule-call/';
        changeScheduleFormLoading(true);
        changeScheduleFormError(false);
        axios.post(url, formValue)
            .then(res => {
                changeScheduleFormLoading(false);
                changeShowNotification(true);
            })
            .catch(err => {
                changeScheduleFormLoading(false);
                changeShowNotification(true);
                changeScheduleFormError(true);
            })
    }

    let getStartedContent;

    if (loading) {
        getStartedContent = <Loading />
    } else if (getStarted) {
        getStartedContent = (
            <Fragment>
                <div className="GetStartedHeader">
                    <h1>{getStarted.heading}</h1>
                </div>
                <div className="Body">
                    <div className="TestimonialBody">
                        <div className="HowToStart">
                            <Middle title="HOW TO START" />
                            <h1 className="InviteOnly">
                                INVITE ONLY?
                            </h1>
                            <p>{getStarted.how_to_start}</p>
                        </div>
                        <div className="JoinWebinars">
                            <h1>Join Our Webinars</h1>
                            <img src={showImg(getStarted.webinar_image)} alt="" />
                            <h1>Lets get you started, Schedule a call</h1>
                            <p>{getStarted.get_started_text}</p>
                        </div>
                    </div>
                </div>
            </Fragment>
        )
    } else if (error) {
        getStartedContent = <ReloadError reload={loadGetStarted} />
    }

    let callTimeContent;
    if (callTime) {
        callTimeContent = callTime.map(call => <option key={call.id} value={call.id}>{call.title}</option>)
    }

    let scheduleForm;

    if (scheduleFormLoading) {
        scheduleForm = <Loading />
    } else {
        scheduleForm = (
            <Fragment>
                <Notification show={showNotification} error={scheduleFormError} >{scheduleFormError ? 'an error occured! Please, Try again' :
                    'Schedule has been submitted!'}</Notification>
                <form onSubmit={submitSchedule} className="ScheduleForm" >
                    <h4>Schedule a zoom call</h4>
                    <p>Email</p>
                    <input type="email" value={formValue.email}
                        onChange={e => updateFormField('email', e.target.value)}
                        placeholder="johndoe@gmail.com" required />

                    <p>Phone no</p>
                    <input type="tel" value={formValue.phone_no} onChange={e => updateFormField('phone_no', e.target.value)}
                        placeholder="+33 039 0944 665" required />

                    <p>Call time</p>
                    <select required value={formValue.call_time} onChange={e => updateFormField('call_time', e.target.value)} >
                        <option value="">Select Call Time</option>
                        {callTimeContent}
                    </select>
                    <button type="submit">Schedule Now</button>
                </form>
            </Fragment>
        )
    }

    return (
        <div className="ImgBackground">
            <div>
                <Header toggleSideNav={val => props.toggleSideNav(val)} />
                {getStartedContent}
                <div className="TestimonialContainer">
                    {scheduleForm}
                    <Footer data={{
                        twitter_url: getStarted ? getStarted.twitter_url : '',
                        instagram_url: getStarted ? getStarted.instagram_url : '',
                        facebook_url: getStarted ? getStarted.facebook_url : '',
                    }} />
                </div>
            </div>
        </div>
    )
}

export default Index;