import React from 'react';
import './Footer.css';
import { Link } from 'react-router-dom';
import Logo from '../../assets/images/Quest_FF-04@2x.png';


const Footer = props => {


    return (
        <div className="FooterContainer">
            <div className="Footer">
                <div className="FooterNavItems">
                    <Link to='/Home' >Home</Link>
                    <Link to='/Testimonials' >Testimonials</Link>
                    <Link to='/Pioneers' >Pioneers</Link>
                    <Link to='/GetStarted' >Get Started</Link>
                </div>
                <div className="FooterLogo">
                    <img src={Logo} alt='IQuest logo' />
                </div>
                <div className="CopyRightSocials">
                    <div className="CopyRight">
                        <p>Copyright 2020 iQuest</p>
                    </div>
                    <div className="Socials">
                        <a href={props.data.facebook_url} ><i className="fab fa-facebook-f"></i></a>
                        <a href={props.data.twitter_url} ><i className="fab fa-twitter"></i></a>
                        <a href={props.data.instagram_url} ><i className="fab fa-instagram"></i></a>
                    </div>
                    <div className="Privacy">
                        <Link to='/Privacy' >Privacy</Link>
                        <Link to='/Disclaimer' >Disclaimer</Link>
                        <Link to='/Sitemap' >Sitemap</Link>
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Footer;