import React, { useState } from 'react';
import { showImg } from '../../CONFIG';


export default props => {

    const [arrowMove, changeArrowMove] = useState(false);

    return (

        <div className="Testimonial">
            <div className="TestimonialLeft">
                <div className="AboutHeader">
                    <p>TESTIMONIAL</p>
                    <div className="AboutLine"></div>
                </div>
                <h1>{props.data.testimonial_heading}</h1>
                <img src={showImg(props.data.testimonial_image1)} alt="testmonial 1" width='100%' />
            </div>
            <div className="TestimonialRight">
                <img src={showImg(props.data.testimonial_image2)} alt="testmonial 2" width='100%' />
                <img src={showImg(props.data.testimonial_image3)} alt="testmonial 3" width='100%' />
                <p>{props.data.testimonial_text}</p>
                <a href="#LearnMore" onMouseEnter={() => changeArrowMove(true)}
                    onMouseLeave={() => changeArrowMove(false)}
                    className="Button">Get Started <span style={{
                        marginRight: arrowMove ? 0 : 10,
                        marginLeft: arrowMove ? 30 : 20
                    }}
                    ><i className="fas fa-arrow-right"></i></span></a>
            </div>
        </div>
    )
}